import "./index.css";
import { Link } from "react-router-dom";

const BasketEmpty = () => {
  return (
    <div className="basket__empty-page">
      <div className="basket__empty-page-img"></div>
      <div className="basket__empty-page-info">
        <h2 className="basket__empty-page-title"> </h2>
        <p className="basket__empty-page-descrpt">    </p>
        <Link to={'/catalog'}>
            <button className="basket__redirect-catalog-btn"> </button>
        </Link>

      </div>
    </div>
  );
};

export default BasketEmpty;
